import axios from 'axios';
import { Cleavage } from '../../../domain/entities/Cleavage';
import { BACKEND_API_URL } from '../../../api/Backend_API_URL';
export class AxiosGlobalCleavageDrawPileGateway {
    constructor(sheme, endpoint, port) {
        const baseURL = `${sheme}://${endpoint}${port ? `:${parseInt(port).toString()}` : ''}`;
        this.backendApiInstance = axios.create({ baseURL });
        console.log(`${this.constructor.name} with backend base url: ${baseURL}`);
    }
    drawGlobalCleavage() {
        return this.backendApiInstance.get(BACKEND_API_URL.GLOBAL_CLEAVAGE_DRAWPILE_DRAW)
            .then(response => {
            console.log(response.data);
            return Promise.resolve(response.data ? new Cleavage(response.data) : undefined);
        })
            .catch(error => Promise.reject(error));
    }
    save(cleavage) {
        return this.backendApiInstance.post(BACKEND_API_URL.GLOBAL_CLEAVAGE_DRAWPILE_SAVE, cleavage)
            .then(response => Promise.resolve(response.data))
            .catch(error => Promise.reject(error));
    }
}
