import { Cleavage } from '../entities/Cleavage';
import { MessageForPlayer } from '../entities/MessageForPlayer';
import { PlayerCleave } from '../entities/PlayerCleave';
export class CleavageApplicationService {
    constructor(publicCleavageDrawPileRepository, globalCleavageDrawPileGateway, currentCleavageRepository, chatGateway, gamePhaseRepository) {
        this.publicCleavageDrawPileRepository = publicCleavageDrawPileRepository;
        this.globalCleavageDrawPileGateway = globalCleavageDrawPileGateway;
        this.currentCleavageRepository = currentCleavageRepository;
        this.chatGateway = chatGateway;
        this.gamePhaseRepository = gamePhaseRepository;
    }
    changeGamePhase(gamePhase) {
        return this.gamePhaseRepository.changeGamePhase(gamePhase);
    }
    retrieveCurrentGamePhase() {
        return this.gamePhaseRepository.retrieveCurrentGamePhase();
    }
    saveGlobalCleavage(cleavage) {
        const currentClevageDto = cleavage.toDto();
        currentClevageDto.players = [];
        return this.globalCleavageDrawPileGateway.save(new Cleavage(currentClevageDto));
    }
    removePlayerOnCleavage(player) {
        return this.loadCleavage()
            .then(cleavage => {
            cleavage.leftChoice.players = cleavage.leftChoice.players.filter(cleavePlayer => cleavePlayer.username !== player.username);
            cleavage.rightChoice.players = cleavage.rightChoice.players.filter(cleavePlayer => cleavePlayer.username !== player.username);
            cleavage.players = cleavage.players.filter(cleavePlayer => cleavePlayer.username !== player.username);
            return this.saveCleavage(cleavage);
        })
            .catch(error => Promise.reject(error));
    }
    addPlayerOnCleavage(player) {
        return this.loadCleavage()
            .then(cleavage => {
            cleavage.players.push(player);
            return this.saveCleavage(cleavage);
        })
            .catch(error => Promise.reject(error));
    }
    isPublicCleavageExist(cleavage) {
        return this.publicCleavageDrawPileRepository.isCleavageExistByTitle(cleavage);
    }
    addPublicCleavage(cleavage) {
        return this.publicCleavageDrawPileRepository.addCleavage(cleavage);
    }
    loadCleavage() {
        return this.currentCleavageRepository.load();
    }
    hasCleavage() {
        return this.currentCleavageRepository.hasCleavage();
    }
    playerCleave(event) {
        return this.currentCleavageRepository.load()
            .then(cleavage => this.onCleave(cleavage, event))
            .catch(error => Promise.reject(error));
    }
    saveCleavage(cleavage) {
        return this.currentCleavageRepository.save(cleavage);
    }
    onCleave(cleavage, event) {
        if (!cleavage.players.some(player => player.username === event.player.username))
            cleavage.players.push(event.player);
        const playerPreviousCleave = this.previousPlayerCleave(cleavage, event);
        return playerPreviousCleave !== PlayerCleave.NOTHING
            ? this.onPlayerAlreadyCleave(event, playerPreviousCleave, cleavage)
            : this.cleave(event, cleavage);
    }
    previousPlayerCleave(cleavage, event) {
        return cleavage.leftChoice.players.some(player => player.username === event.player.username)
            ? PlayerCleave.LEFT
            : cleavage.rightChoice.players.some(player => player.username === event.player.username)
                ? PlayerCleave.RIGHT
                : PlayerCleave.NOTHING;
    }
    cleave(event, cleavage) {
        if (event.playerCleave === PlayerCleave.LEFT)
            cleavage.leftChoice.players.push(event.player);
        if (event.playerCleave === PlayerCleave.RIGHT)
            cleavage.rightChoice.players.push(event.player);
        return this.saveCleavage(cleavage);
    }
    onPlayerAlreadyCleave(event, previousPlayerCleave, cleavage) {
        return event.playerCleave === previousPlayerCleave
            ? this.chatGateway.sendMessageToPlayer(new MessageForPlayer(event.player, `You have still cleave ${previousPlayerCleave}`))
            : this.uncleave(event, previousPlayerCleave, cleavage)
                .then(cleavage => this.cleave(event, cleavage))
                .catch(error => Promise.reject(error));
    }
    uncleave(event, previousPlayerCleave, cleavage) {
        if (previousPlayerCleave === PlayerCleave.LEFT)
            cleavage.leftChoice.players = cleavage.leftChoice.players.filter(player => player.username !== event.player.username);
        if (previousPlayerCleave === PlayerCleave.RIGHT)
            cleavage.rightChoice.players = cleavage.rightChoice.players.filter(player => player.username !== event.player.username);
        return Promise.resolve(cleavage);
    }
    nextPublicCleavage() {
        return this.publicCleavageDrawPileRepository.nextCleavage();
    }
    randomGlobalCleavage() {
        return this.globalCleavageDrawPileGateway.drawGlobalCleavage();
    }
}
