import { Player } from './Player';
export class Choice {
    constructor(choiceDto) {
        this.name = choiceDto.name;
        this.players = choiceDto.players.map(player => new Player(player));
    }
    toDTO() {
        return {
            name: this.name,
            players: this.players.map(player => player.toDto())
        };
    }
}
