export class MessageForPlayer {
    constructor(player, message) {
        this.player = player;
        this.message = message;
    }
}
export const noCleavagePleaseWait = (player) => ({
    player: player,
    message: `Il n'y a pas de clivage en cours ${player.username}. Attends un peu.`
});
export const welcomePlayerMessage = (player) => ({
    player,
    message: welcomeMessage(player)
});
const welcomeMessage = (player) => `Bienvenue ${player.username} au jeu du clivage! Tu peux tapper '!h' pour l'aide.`;
