import { ApplicationEvent } from '../GameEvent';
import { EventType } from '../EventType';
export class LaunchCleavageEvent extends ApplicationEvent {
    constructor(cleavageTitle, leftChoiceName, rightChoiceName) {
        super();
        this.cleavageTitle = cleavageTitle;
        this.leftChoiceName = leftChoiceName;
        this.rightChoiceName = rightChoiceName;
        this.eventType = EventType.LAUNCH_CLEAVAGE;
    }
}
