export const retreiveRecordByOffsetErrorMultipleEntities = (kind, offset) => ` Multiple entities of kind '${kind}' at offset '${offset}' ?!`;
export const retreiveRecordByOffsetErrorNoEntity = (kind, offset) => ` No entity of kind '${kind}' at offset '${offset}'`;
export const entityRetreivedAtOffset = (entities, query) => `  ${entities.length} entities retrieved on kind '${query.kinds}' at offset '${query.offsetVal}'.`;
